/*** Gen. Layout  - Widths, Margins, Paddings ***/
.row {
  &#page-content,
  &#page-header {
    max-width: 1170px;
    margin: 0 auto;
  }
}
/*** Gen. Layout  - Colors, Logo etc ***/
.card {
  background-color: transparent;
  border: 0;

  section[data-region="blocks-column"] {
    background-color: #e5e5e4;
  }

  #page-header & {
    background-color: transparent;

    &,
    & * {
      color: white;
    }
  }
}
.coursebox {
  background-color: $trans-white !important;
  padding: 0.5rem;
}
#page-header .logo img {
  max-height: 100px;
}
#page-content {
  background-color: #F2F2F2;
}
table {
  width: 100%;
  border-collapse: collapse;
  border-spacing: 0;
}
td,
th {
  vertical-align: middle;
  li,
  ul {
    list-style: disc;
  }
  &{
    padding: 0.5rem 1rem;
    border-left: 1px solid $page-content;
  }
  tr:nth-child(even) & {
      background-color: darken($page-content,5%);
  }
  tr:nth-child(odd) & {
      background-color: lighten($page-content,1%);
  }
  /*tr &:first-child {
    font-weight: 700;
  }*/
}
/**** CONTENT ***/

/*** StartSeite Layout Thumbnails ***/
.frontpage-course-list-enrolled {
  display: block;
  @media screen and (min-width:992px) and (max-width:1365px) {
    body.pagelayout-frontpage & {
      @include kacheln;
    }

    body.pagelayout-frontpage.drawer-open-left & {
      @include list;
    }
  }
  @media screen and (min-width:1365px) {
    @include kacheln;
  }
}
/*** Dashboard*** - .pagelayout-mydashboard .card,***/


/** Courses **/
#intro {
  @include intro-style;
}

.summary {
  .topics li.section {
    margin-left: 0;
  }

  .topics li:first-child & {
    margin: 0 0 2rem 0 !important;
    @include intro-style;
  }

  #region-main & + ul.section {
    margin: 0;
    padding: 1em 0;
  }
}
.section {
  &.activity {
    padding: 0.5em 0;
  }
  &:nth-child(even) {
    background: rgba(255,255,255,.5);
  }
  &.label {
    background-color: transparent;

    p {
      font-weight: 600;

      &:first-child {
        margin-top: 2em;
      }
    }
  }
  .editing &:hover {
    background-color: transparent;
    cursor: pointer;
  }
  li.activity {
    padding: 0.2em 0 0.2rem 1rem !important;
  }
  &.current {
    border: 0;
    /*    background: lighten($brand-info,15%);*/
    background: rgba(255,255,255,.75);
    padding: 1em;
    margin: 0 -1rem;

    &:before {
      display: none;
    }
  }
  .activity.forum.modtype_forum {
    text-align: center;
    background-color: $brand-info;
    margin:2rem 0;
    &:hover{opacity: .75;}
    }
}
