/*** Buttons ***/
.btn-default,
.btn-secondary {
  background-color: $brand-primary;
  border-color: darken($brand-primary,25%);
  color: white !important;

  [data-region="drawer-toggle"] & {
    border: 0;
  }

  &:active,
  &:focus,
  &:hover {
    background-color: $light-hover-color;
    border: 0;
    color: white;
  }
}

.numberofcourse {
  background: $brand-primary;
  color: $white;
  display: inline-flex;
  padding: 0.25rem;
  height: 1.2rem;
  width: 1.2rem;
  align-items: center;
  justify-content: center;
  margin-left: 1rem;
  border-radius: 1rem;
}
